<template>
  <div id="app">
    <navbar v-show="this.$store.state.amShow"></navbar>
    <!-- <sidebar></sidebar> -->
    <router-view ></router-view>
    <tabbar v-show="isShow"></tabbar>
        <city-icon v-show="this.$store.state.hideCity"></city-icon>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
// import sidebar from '@/components/Sidebar'
import tabbar from '@/components/Tabbar'
import bus from '@/views/bus/bus.js'
// import axios from 'axios'
import CityIcon from '@/views/CityIcon'
export default {
  data () {
    return {
      isShow: true,
      dataCity: []
    }
  },
  components: {
    Navbar,
    // sidebar,
    tabbar,
    CityIcon
  },
  beforeMount () {
    bus.$on('maizuo', (data) => {
      this.isShow = data
    })
  },
  mounted () {
    if (localStorage.getItem('cityName') === null) {
      localStorage.setItem('cityName', '上海')
    }
    if (localStorage.getItem('cityId') === null) {
      localStorage.setItem('cityId', '310100')
    }
    // if (this.$store.state.hideCity === false) {
    //   this.$store.commit('HideCity0', true)
    // }
  }
}
</script>

